// SERVICES - MEANS, THAT THIS COMPONENTS INTERACT WITH OTHER API/SITES ETC
import {useHttp} from '../hooks/http.hook';

const useMarvelService  = () => {
    const {loading, request, error, clearError} = useHttp();

    const _apiBase = 'https://gateway.marvel.com:443/v1/public/';
    const _apiKey = 'apikey=8385adae5442e4919303b800b093cd65';
    const _baseOffset = Math.floor(Math.random() * (1500 - 100 + 1)) + 100;
    const _comicsOffset = Math.floor(Math.random() * (50000 - 100 + 1)) + 100;
    
    const getAllChars = async (offset = _baseOffset) => {
        const result = await request(`${_apiBase}characters?limit=9&offset=${offset}&${_apiKey}`);
        return result.data.results.map(_transformChar);
    }

    const getChar = async (id) => {
        const result = await request(`${_apiBase}characters/${id}?${_apiKey}`);
        return _transformChar(result.data.results[0]);
    }

    const getAllComics = async (offset = _comicsOffset) => {
        const result = await request(`${_apiBase}comics?limit=8&offset=${offset}&${_apiKey}`);
        return result.data.results.map(_transformComics);
    }

    const getComic = async (id) => {
        const result = await request(`${_apiBase}comics/${id}?${_apiKey}`);
        return result.data.results.map(_transformComics)[0];
    }

    const _transformChar = (char) => {
        return {
            name: char.name,
            id: char.id,
            desc: char.description ? char.description : 'There is no data about this character...',
            thumbnail: char.thumbnail.path + '.' + char.thumbnail.extension,
            homepage: char.urls[0].url,
            wikipage: char.urls[1].url,
            comics: char.comics.items,
        }
    }

    const _transformComics = (comic) => {
        return {
            id: comic.id,
            title: comic.title,
            price: `${comic.prices[0].price === 0 ? 'NOT AVAIVABLE' : comic.prices[0].price + '$'}`,
            thumbnail: comic.thumbnail.path + '.' + comic.thumbnail.extension,
            link: `${comic.resourceURI}&${_apiKey}`,
            pages: comic.pageCount ? `Pages: ${comic.pageCount}` : 'Pages: no info',
            // pages: comic.pageCount,
            languages: comic.textObjects.length >= 1 ? comic.textObjects[0].language : 'no info',
            desc: comic.textObjects.length >= 1 ? comic.textObjects[0].text : 'There\'s no description about this comic...',
        }
    }

    return {loading, error, getAllChars, getChar, _baseOffset, _comicsOffset, clearError, getAllComics, getComic};
}

export default useMarvelService;